@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
/* using roboto and montserrat font family */

:root {
	--normal-text: 2rem;
}

.bgcolor {
	background-color: #1c1d25;
}

.pagebackdrop {
	background-image: url(/portfolio/static/media/codingimage.51a561af.jpg);
	height: 500px;
	width: auto;
	background-size: cover;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: flex-start;
}

.socials {
	margin-left: 1em;
}

.socials a {
	margin-left: 0.5em;
	color: white;
	text-decoration: none;
}

.socials a:hover {
	margin-left: 0.5em;
	color: white;
	text-decoration: none;
	font-weight: bold;
	border-bottom: 2px solid white;
}

.bannerphoto {
	background-color: #1c1d25;
	height: 100vh;
	width: auto;
	background-size: cover;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: flex-start;
}

.avatar {
	height: 300px;
	border-radius: 1em;
	box-shadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.7);
}

.projectImage {
	height: 175px;
	width: 300px;
}

.boxshadow {
	box-shadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.7);
}

.card-body:hover {
	color: white;
	text-decoration: none;
	background-color: rgb(102, 178, 204);
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

nav {
	font-size: 20px;
	padding: 0.5em;
	color: white;
	background-color: #1c1d25;
	display: flex;
	justify-content: space-between;
}

ul {
	display: flex;
	list-style: none;
	width: 25vw;
	justify-content: flex-end;
}

li {
	width: 200px;
}

li a {
	color: white;
	-webkit-text-emphasis: none;
	        text-emphasis: none;
	text-decoration: none;
	margin-left: 0.5em;
}
li a:hover {
	color: white;
	-webkit-text-emphasis: none;
	        text-emphasis: none;
	text-decoration: none;
	margin-left: 0.5em;
	font-weight: bolder;
}

.NavbarStyles_navlogo__3K7jG {
	font-size: 25px;
}

.BannerStyles_bannertext__1eaSj {
	font-family: "Roboto Mono", sans-serif;
	font-size: 40px;
}

.BannerStyles_bannerstyle__3Ian3 {
	height: 100vh;
	color: white;
	background-color: #1c1d25;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

