nav {
	font-size: 20px;
	padding: 0.5em;
	color: white;
	background-color: #1c1d25;
	display: flex;
	justify-content: space-between;
}

ul {
	display: flex;
	list-style: none;
	width: 25vw;
	justify-content: flex-end;
}

li {
	width: 200px;
}

li a {
	color: white;
	text-emphasis: none;
	text-decoration: none;
	margin-left: 0.5em;
}
li a:hover {
	color: white;
	text-emphasis: none;
	text-decoration: none;
	margin-left: 0.5em;
	font-weight: bolder;
}

.navlogo {
	font-size: 25px;
}
